import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import {withRouter} from 'react-router-dom'
import connect from 'connect'
import { List, NavBar, Tabs, WhiteSpace, Badge, Card, Checkbox, Button, Modal} from 'antd-mobile';
const alert = Modal.alert;
@connect
@withRouter
class Home extends React.Component {
    constructor(props){
        super(props);
        this.state = {
          navTop: false,
          multiple: false,
          onedit: false
        }

       
    }

    
    componentDidMount () {
       
    }

      onChange= (value) => {
        this.setState({ value });
      };
      clear = () => {
        this.setState({ value: '' });
      };
      handleClick = () => {
        this.manualFocusInst.focus();
    }

    edit() {
        this.setState({
            onedit: !this.state.onedit
        });
    }

    del() {
        alert('温馨提示', '您确定要删除所选内容吗？', [
          { text: '取消', onPress: () => {console.log('cancel')} },
          { text: '确定', onPress: () => {console.log('ok')} },
        ])

    }




    render () {
      const tabs2 = [
        { title: '头条', sub: '1' },
        { title: '作品', sub: '2' },
      ];
      const data = [
        {
          img: require('../../images/lian.png'),
          title: 'Meet hotel',
          des: '新闻标题新闻标题新闻标题',
          value: 0,
        },
        {
          img: require('../../images/lian.png'),
          title: 'McDonald\'s invites you',
          des: '新闻标题新闻标题新闻标题',
          value: 1,
        },
        {
          img: require('../../images/lian.png'),
          title: 'Eat the week',
          des: '新闻标题新闻标题新闻标题',
          value: 2,
        },
      ];
        const Item = List.Item;
        const Brief = Item.Brief;
        const CheckboxItem = Checkbox.CheckboxItem;
        const AgreeItem = Checkbox.AgreeItem;
       const { onedit } = this.state
        return (
            <div className="collect-container">
              <NavBar
                   mode="light"
                   icon={<img src={require('../../images/back.png')} alt="LOGO" className="back"  onClick={this.props.history.goBack}/>}
                   rightContent={<span onClick={() => this.edit()}>{onedit ? '完成' : '编辑'}</span>}
                 >收藏</NavBar>
                <Tabs  tabBarInactiveTextColor={'#666'} tabBarBackgroundColor={'#fff'} tabBarUnderlineStyle={{border: '1px solid #A55304'}} tabBarActiveTextColor={'#A55304'} tabs={tabs2} initialPage={0} animated={false} useOnPan={false}>
                   
                        {
                            !onedit ?  <div style={{ flex:1 , backgroundColor: '#efefef', margin: '0 auto' }}>
                            {data.map((obj, index) => (
                        <div  key={'myordercard' + index} >
                         <Card full={true}>
                               <Card.Header
                                 thumb={obj.img}
                                 thumbStyle={{width: '1rem',height: '0.75rem', borderRadius: '0.03rem'}}
                                 extra={
                                  <div style={{ }}>
                                    <div className="destext">{obj.des}</div>
                                    <div><span className="jiatext">2017-02-25. 浏览：10102次</span></div>
                                    <div className="mid"></div>
                                  </div>
                                 }
                               />
                         </Card>
                      </div>
                       ))}
                       </div> :  <List>
                      {data.map((obj, index) => (
                        <div key={'collect1' + index} style={{ flex:1 , backgroundColor: '#efefef', margin: '0 auto' }}>
                        <CheckboxItem key={obj.value} onChange={() => this.onChange(obj.value)}  key={index}>
                          <div>
                            <Card full={true}>
                                  <Card.Header
                                    thumb={obj.img}
                                    thumbStyle={{width: '1rem',height: '0.75rem', borderRadius: '0.03rem'}}
                                    extra={
                                     <div style={{ }}>
                                       <div className="destext">{obj.des}</div>
                                       <div><span className="jiatext">2017-02-25. 浏览：10102次</span></div>
                                        <div className="mid"></div>
                                     </div>
                                    }
                                  />
                            </Card>
                          </div>
                        </CheckboxItem>
                        
                        </div>
                      ))}
                    </List>
                   
                        }
                       
                    
                        {
                            !onedit ?  <div style={{ flex:1 , backgroundColor: '#efefef', margin: '0 auto' }}>
                            {data.map((obj, index) => (
                        <div  key={'collect2' + index} key={'myordercard' + index} >
                         <Card full={true}>
                               <Card.Header
                                 thumb={require('../../images/lian.png')}
                                 thumbStyle={{width: '3.36rem',height: '1.87rem'}}
                               />
                               <Card.Body>
                                 <div>名称名称名称名称名称名称名称名称名称</div>
                               </Card.Body>
                               <Card.Footer content={<div><span  className="hongtext">￥20000.00</span><span className="futext">今日价</span></div>} />
                             </Card>
                      </div>
                       ))}
                       </div> :  <div>
                      {data.map((obj, index) => (
                        <div key={'collect3' + index}>
                         <Card full={true}>
                                <CheckboxItem key={obj.value} onChange={() => this.onChange(obj.value)}  key={index}>
                                </CheckboxItem>
                               <Card.Header
                                 thumb={require('../../images/lian.png')}
                                 thumbStyle={{width: '3.36rem',height: '1.87rem'}}
                               />
                               <Card.Body>
                                 <div>名称名称名称名称名称名称名称名称名称</div>
                               </Card.Body>
                               <Card.Footer content={<div><span  className="hongtext">￥20000.00</span><span className="futext">今日价</span></div>} />
                             </Card>
                        </div>
                      ))}
                    </div>
                    
                        }
                   
                </Tabs>
                {onedit ? <CheckboxItem key="all" data-seed="logId" className="allcell" >
                    <div style={{ display: '-webkit-box', display: 'flex'}}>
                    <span className="bottomall">全选</span>
                    <Button type="warning" onClick={() => this.del()}>删除(2)</Button>
                  </div>
                </CheckboxItem> : null}
             
            </div>
        )
    }
}
export default Home


